$blackColor: #333;
$whiteColor: #fff;
$greyColor: #e8e8e8;
$primaryColor: #08aef1;
$secondaryColor: #d9ab4d;
$freeSpace: 15px;
$headerHeight: 49px;
$footerHeight: 49px;

@import "reset";
@import "grid";
@import "typography";

html {
    display: flex;
    flex-direction: column;
    min-height: 100%;
}

body {
    display: flex;
    flex-direction: column;
    flex: auto;
    color: $blackColor;
    background-color: $whiteColor;
    padding-top: $headerHeight + $freeSpace;
}

@import "header";
@import "main";
@import "footer";
