* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

textarea,
input,
button {
  outline: none; }

.container {
  align-self: center;
  display: flex; }

/* Portrait tablets and medium desktops */
@media (min-width: 992px) {
  .container {
    width: 970px; } }

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .container {
    width: 750px; } }

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .container {
    width: 100%; } }

html {
  font-size: 0.875em; }

body {
  font-family: "Lucida Grande", "Lucida Sans Unicode", Arial, Helvetica, Verdana, sans-serif;
  font-weight: 400;
  line-height: 1.45; }

p {
  margin-bottom: 1.3em; }

h1,
h2,
h3,
h4 {
  margin: 1.414em 0 0.5em;
  font-weight: inherit;
  line-height: 1.2; }

h1 {
  margin-top: 0;
  font-size: 2.441em; }

h2 {
  font-size: 1.953em; }

h3 {
  font-size: 1.563em; }

h4 {
  font-size: 1.25em; }

small,
.font_small {
  font-size: 0.8em; }

html {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

body {
  display: flex;
  flex-direction: column;
  flex: auto;
  color: #333;
  background-color: #fff;
  padding-top: 64px; }

.header {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 49px;
  font-size: 15px;
  background-color: #08aef1;
  opacity: 0.9;
  z-index: 1000; }
  .header .container {
    height: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px 0 15px; }
    .header .container .logo {
      background-image: url("../images/logo.jpg");
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      width: 120px;
      height: 46px;
      margin-top: 3px; }
    .header .container .menu {
      display: flex;
      list-style: none;
      height: 100%;
      align-items: center; }
      .header .container .menu li a {
        color: #fff; }

.main {
  display: flex;
  flex-direction: column;
  flex-grow: 1; }
  .main .container .content {
    flex: 3;
    margin-right: 15px;
    margin-bottom: 15px;
    padding: 15px;
    min-height: 100px;
    border: 1px solid #e8e8e8; }
  .main .container .sidebar {
    flex: 1;
    position: relative;
    display: flex;
    flex-direction: column; }
    .main .container .sidebar .box {
      margin-bottom: 15px;
      padding: 15px;
      width: 100%;
      min-height: 100px;
      border: 1px solid #e8e8e8; }

body {
  background-color: black; }

.footer {
  display: flex;
  flex-direction: column;
  height: 49px;
  border-top: 1px solid #e8e8e8;
  background-color: #fff; }
  .footer .container {
    height: 100%;
    line-height: 49px; }
